.modal {
  display: flex;
  flex-direction: column;
  align-items: end;
  justify-content: center;


  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  width: 450px;
  padding: 25px;
  height: 280px;
  position: absolute;
  background-color: white;
  border: 2px solid #111111;
  border-radius: 15px;
  z-index: 3;
  margin-bottom: 20px;

  @media only screen and (max-width: 681px) and (min-width: 0px) {
    width: 300px;
    height: 300px;
  }

  .btnClose {
    width: 10px;
    font-size: 20px;
    color: #0a0a0a;
    font-weight: bold;
    cursor: pointer;
    margin-top: 0;

    @media only screen and (max-width: 680px) and (min-width: 0px) {
      margin-top: 10px;
      margin-right: -5px;
    }
  }

  .textModalBlock {
    margin-left: 20px;
    margin-right: 20px;
    display: flex;
    flex-direction: column;
    text-align: center;

    .modalMessage {
      display: flex;
      justify-content: center;
      flex-direction: column;
      align-items: center;
      font-size: 18px;
      color: #0a0a0a;
      margin-bottom: 10px;

    }
  }
}