@import "../Common/Styles/Sass/variables";

.projectsBlock {
  height: 100%;
  background-color: $bgColor;
  color: $primaryColor;
  font-family: $titleFontFamily;
  position: relative;
  z-index: 1;

  .projectsContainer {
    flex-direction: column;
    justify-content: flex-start;
    flex-wrap: wrap;

    .projects {
      display: flex;
      width: 100%;
      flex-wrap: wrap;
      justify-content: space-around;
      margin-top: 30px;
    }
  }
}
